
<template>
  <div>
    <LayOut>
      <template #content>
        <div class="container">
          <div v-if="!chartPie" class="ad"></div>
          <a-row>
            <a-col :span="13">
              <div id="myMap" ref="myMap" style="height: 600px;"></div>
            </a-col>
            <a-col :span="11">
              <div id="myPie" ref="myPie" style="height: 600px;"></div>
            </a-col>
          </a-row>
        </div>
      </template>
    </LayOut>
  </div>
</template>


<script>
import { getCordinate } from "@/api/api";
import { getCountByDistrict } from "@/api/api";
import { getAssistantCount } from "@/api/api";
import { getDisabledCount } from "@/api/api";  //queryDistrictCordinate
import { queryDistrictCordinate } from "@/api/api";
import LayOut from "@/views/LayOut.vue";
let echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar"); // 引入柱状图组件
require("echarts/lib/chart/pie"); // 引入饼状图组件
require("echarts/map/js/china.js"); // 引入中国地图组件
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
require("echarts/theme/macarons"); //引入主题
require("echarts/theme/shine"); //引入主题

import option from "@/api/map-option";
export default {
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      chart: null,
      chartPie: null,
      mapdata: [],
      disabledtotal: 0,
      assistanttotal: 0
    };
  },
  async mounted() {
    this.disabledtotal = await getDisabledCount();
    this.assistanttotal = await getAssistantCount();
    await this.getmapdata();
    this.drawChinaMap();
    this.drawPie();
  },
  updated() {
    if (!this.chart || !this.chartPie) {
      this.initChart();
    }
  },
  beforeDestroy() {
    if (!this.chart && !this.chartPie) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
    this.chartPie.dispose();
    this.chartPie = null;
  },
  computed: {},
  methods: {
    async getmapdata() {
      //let res = await Promise.all(region.map(v => getCountByDistrict(v)));
      let cordinates = await queryDistrictCordinate();
      //console.log("cors",cordinates)
      let res = await getCountByDistrict(); //查询格式  {"_id": "上海市普陀区宜川街道","total": 82}

      for (let i = 0; i < res.length; i++) {
        let obj = {};
        cordinates.map(v => {
          if (res[i]._id == v.district) {
            let mapdata_arr = [...v.cordinate, res[i].total];
            obj = {
              name: res[i].total,
              value: mapdata_arr,
              district: v.district,
              shortname: v.shortname
            };
            this.mapdata.push(obj);
          }
        }); //end of  map
      }
    },
    drawChinaMap() {
      this.chart = this.$echarts.init(
        document.getElementById("myMap"),
        "macarons"
      );
      option.series[0].data = this.mapdata;
      option.title.subtext = `受助人员总数: ${this.disabledtotal}    服务人员总数:${this.assistanttotal}`;
      this.chart.setOption(option);
    },
    drawPie() {
      this.chartPie = this.$echarts.init(document.getElementById("myPie"));
      let mydata = [];
      for (let i = 0; i < this.mapdata.length; i++) {
        let val = this.mapdata[i].value[2];
        mydata.push({ value: val, name: this.mapdata[i].shortname });
      }

      let option = {
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: "65%",
            data: mydata,
            // roseType: "angle",
            itemStyle: {
              normal: {
                shadowBlur: 200,
                shadowColor: "rgba(0, 0, 0, 0.5)"
              }
            },
            label: {
              normal: {
                position: "outer", // 设置标签位置，默认在饼状图外 可选值：'outer' ¦ 'inner（饼状图上）'
                // formatter: '{a} {b} : {c}个 ({d}%)'   设置标签显示内容 ，默认显示{b}
                // {a}指series.name  {b}指series.data的name
                // {c}指series.data的value  {d}%指这一部分占总数的百分比
                formatter: "{b}:{c}"
              }
            }
          }
        ]
      };
      this.chartPie.setOption(option);
    }
  },
  components: {
    LayOut
  }
};
</script>

<style scoped>
.container {
  overflow: auto;
}
.ad {
  margin: 0px;
  background: url("../assets/home_page.png") no-repeat;
  background-size: 100% 100%;
  height: 600px;
  position: absolute;
  width: 1500px;
}
</style>