import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
  
  state: {
    status: '',
    user: sessionStorage.getItem('user') || '',
    mapdata:[]
  },
  
  getters: {
    isLoggedIn: state => !!state.user, // !!将state.user强制转换为布尔值，若state.user存在且不为空(已登录)则返回true，反之返回false
    authStatus: state => state.status
  },
  mutations: {
     user_signin :  (state,user)=>{
         sessionStorage.setItem('user', JSON.stringify(user)) 
         state.user =  JSON.stringify(user)  
         state.status = 'success';            
       },
    SIGN_OUT(state) { //退出，删除状态
        sessionStorage.removeItem('user'); //移除sessionStorage
        state.status = ''; 
        state.user = ''; 
    },
  },
  actions: {
    user_signin:({commit},user)=>commit('user_signin'),
    SIGN_OUT:({commit})=>commit('SIGN_OUT'),
    
  }
})

export default store