export default {
    
        backgroundColor: '#404a59',
        title: {
            text: '上海壹方助残服务区位分布图',
            // subtext: '副标题',
            // sublink: 'http://www.pm25.in',
            left: 'center'
        },
        //地理坐标系组件用于地图的绘制，支持在地理坐标系上绘制散点图，线集。
        //要显示散点图，必须填写这个配置
        geo:{
            show:true,
            roam: true, //是否允许鼠标滚动放大，缩小
            map:'上海',
            
            label: {  //图形上的文本标签，可用于说明图形的一些数据信息
                show:true,  //是否显示文本
                color:'#CCC',  //文本颜色
            },
            itemStyle: { //地图区域的多边形 图形样式。 默认样试。
                areaColor: '#323c48', //地图区域的颜色。
                borderColor: '#111', //边框线
            },
            emphasis:{ //高亮状态下的多边形和标签样式。
                label:{ //文本
                    color: '#ADA',
                },
                itemStyle:{ //区域
                    areaColor: '#F60'
                }
            },
            zoom:8
        },
        center:[121.413799,31.25661],
        tooltip:{
            show:true,
            formatter: function(value) {
                return value.data.district    
            }
        },
        //是视觉映射组件，用于进行『视觉编码』，也就是将数据映射到视觉元素（视觉通道）。
        visualMap: {
            min: 0, //最小值
            max: 250, //最大值
            calculable: true, //是否显示拖拽用的手柄（手柄能拖拽调整选中范围）。
            inRange: {
                color: ['#e83e5e','#fb2a08', '#d6f628'] //颜色
            },
            textStyle: {
                color: '#fff'
            },
        },
        series:[
            {
                type:'effectScatter', //样试
                coordinateSystem:'geo', //该系列使用的坐标系
                data:[ //数据
                    {name: '普陀区', value: [121.413799,31.25661,530]},
                    {name: '黄浦区', value: [121.49027,31.231255,440]},
                    {name: '长宁区', value: [121.391181,31.218239,400]}
                ],
                itemStyle: { //样试。
                    normal:{ //默认样试
                        color: '#d6f628'
                    }
                },
                label: {
                    normal: {
                        formatter: '{b}',
                        position: 'right',
                        show: true
                    }
                },
                //标记的大小,可以设置数组或者函数返回值的形式
                symbolSize: function (val) {
                    return val[2] / 3;
                },
                rippleEffect: { //涟漪特效相关配置。
                    brushType: 'stroke' //波纹的绘制方式
                },
                hoverAnimation: true, //鼠标移入放大圆
            }
        ]
}
