export const loginUserPost = async (params) =>{
    let url = `${baseurl}/users/login`
    let res = await axios.post(url,params)
    let result = res.data
    return result.data[0]
}

const getToken =  async () =>{
    let url = `${baseurl}/token`
    return await axios.get(url)
}
