const roleManager =  (data) =>{  //权限管理
    
    const {role,username} =  JSON.parse(store.state.user)
    if(role != 0){
        let arr = [];
        data.map(x =>{
            if(x.administrator == username) arr.push(x) //如果某个数据的管理员字段是当前用户，就存入arr数组
        })
        return arr //返回符合权限的
    }else return data
}


export const initDisableds =  async () =>{
    const cloudurl= `${baseurl}/disabled/getdisabledinit`;
    const res = await axios.post(cloudurl)
    const result = JSON.parse(res.data.resp_data).data 
    
    return roleManager(result)

}

export const queryDisableds =   async (params)  =>{
    let cloudurl= `${baseurl}/disabled/getdisableds`;
    let res = await axios.post(cloudurl,params)
    let result = JSON.parse(res.data.resp_data).data 
    return roleManager(result)
}

export const queryDisabledByName  = async (params)  =>{
    let cloudurl= `${baseurl}/disabled/getdisabledbyname`;
    let res = await axios.post(cloudurl,{real_name:params})   
    let result = JSON.parse(res.data.resp_data).data 
    return roleManager(result)
}
