          <a-menu
            mode="inline"
            :default-selected-keys="[$route.path]"
            :default-open-keys="['sub1','sub2','sub3','sub4']"
            style="height: 100%"
          >
            <a-sub-menu key="sub1">
              <span slot="title">
                <a-icon type="user" />用户信息
              </span>
              <a-menu-item key="/disabled">
                <router-link to="disabled">用户查询</router-link>
              </a-menu-item>
              <a-menu-item key="/savedisabled">
                <router-link to="savedisabled">用户录入</router-link>
              </a-menu-item>
            </a-sub-menu>
            <a-sub-menu key="sub2">
              <span slot="title">
                <a-icon type="team" />服务人员
              </span>
              <a-menu-item key="/assistant">
                <router-link to="assistant">服务人员查询</router-link>
