              </a-menu-item>
            </a-sub-menu>
            <a-sub-menu key="sub4">
              <span slot="title">
                <a-icon type="solution" />签到考勤
              </span>
              <a-menu-item key="/queryclock">
                <router-link to="queryclock">考勤记录统计</router-link>
              </a-menu-item>
            </a-sub-menu>
          </a-menu>
