<script>
export default {
  computed: {
    username() {
      let user = this.$store.state.user
        ? JSON.parse(this.$store.state.user)
        : { username: null };
      return user.username;
    }
  },
  methods: {
    loginout() {
      this.$store.dispatch("SIGN_OUT");
      this.$router.push("/login");
    }
  }
};
</script>